:root {
  --primary: #fbcfe8;
  --secondary: #d8b4fe;
  --text-dark: #374151;
  --bg: #fff0f6;
  --card-bg: #ffffff;
  --card-border: #e5e7eb;
  --link-color: #9333ea;
  --link-hover: #7c3aed;
}

body.dark {
  --primary: #7c3aed;
  --secondary: #6d28d9;
  --text-dark: #d1d5db;
  --bg: #1f2937;
  --card-bg: #374151;
  --card-border: #4b5563;
  --link-color: #a78bfa;
  --link-hover: #c4b5fd;
  background-color: var(--bg);
  color: var(--text-dark);
}

body, html, #root {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  background-color: var(--bg);
  color: var(--text-dark);
  font-family: 'Poppins', sans-serif;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.App {
  max-width: 900px;
  margin: auto;
  padding: 2rem;
}

.hero {
  background: linear-gradient(to right, var(--primary), var(--secondary));
  padding: 3rem 2rem;
  border-radius: 1.5rem;
  text-align: center;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--bg);
  border-bottom: 1px solid var(--card-border);
  padding: 1rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  z-index: 100;
}

.nav a {
  text-decoration: none;
  color: var(--link-color);
  font-weight: bold;
}

.nav a:hover {
  color: var(--link-hover);
}

section {
  scroll-snap-align: start;
  min-height: 100vh;
  padding: 6rem 2rem 2rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.project-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
  margin: 2rem 0;
}

.project-card {
  width: 250px;
  height: 180px;
  padding: 1rem;
  background-color: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 1rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  transition: all 0.3s ease;
}

.contact a {
  color: var(--link-color);
  text-decoration: none;
}

.contact a:hover {
  text-decoration: underline;
  color: var(--link-hover);
}

.qa-box {
  background: var(--card-bg);
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.05);
  margin-top: 3rem;
  transition: background-color 0.3s ease;
}

.tooltip {
  position: relative;
  cursor: pointer;
  display: inline-block;
}

.tooltiptext {
  visibility: hidden;
  background-color: #fdf6f0;
  color: #333;
  text-align: center;
  border-radius: 8px;
  padding: 0.5rem;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.75rem;
  white-space: nowrap;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
